import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle, faFingerprint } from "@fortawesome/free-solid-svg-icons";
import $ from "jquery";
import axios from "axios";
import "./FindModal.css";

class FindModal extends React.Component {
    state = { // phase 1 : input email , phase 2 : email varification, phase 3 : change password
        phase: 1
    }

    clickClose = () => {
        document.getElementById("find_email").value = "";
        document.getElementById("find_modal").classList.add("md_hidden");
        $("#find_email").removeAttr("readOnly");
        $(".find_btn").addClass("btn_hide");
        this.setState({phase : 1});
    }

    clickSendEmail = async () => {
        const distEmail = $("#find_email").val();
        // eslint-disable-next-line no-restricted-globals
        const confirmEmail = confirm(`${distEmail}로 메일을 보내시겠습니까?`);
        
        const {data} = await axios.get(`/api/user/find/cert/${distEmail}`);
        if(data.isUser === false) alert("가입된 사용자가 아닙니다. 이메일을 확인해주세요.");
        else {
            if(data.isSend) alert("이메일을 전송하였습니다.");
        }

        $("#find_email").attr("readOnly", "ture");
        if(confirmEmail) this.setState({phase : 2});
    }

    clickCertificate = async () => {
        const distEmail = $("#find_email").val();
        const certNum = $("#find_certNum").val();
        // 인증 요청
        const {data} = await axios.get(`/api/user/find/cert/confirm/${distEmail}/${certNum}`);
        // 인증 성공시
        if(data.isCert) {
            const {data} = await axios.get(`/api/user/find/temppwd/${distEmail}`);
            if(data.isSend) {
                alert("인증되었습니다. 이메일로 임시비밀번호를 전송하였습니다. 로그인 후 비밀번호를 변경하여주세요.");
                this.clickClose();
            }
        } else alert("인증번호를 다시 확인하여 주십시오.")

        
    }

    render() {
        const { phase } = this.state;
        return (
            <div id="find_modal" className="find_modal md_hidden">
                <div className="find_wrap">
                    <div className="find_header">
                        <h4><FontAwesomeIcon icon={faFingerprint} size="lg" />&nbsp;&nbsp;Find</h4>
                    </div>
                    <div className="find_content">
                        <input
                            id="find_email"
                            type="email"
                            name="email"
                            placeholder="input your email"
                            autoComplete="off"
                        />
                        {phase < 3 ? 
                            <button className="find_send_email" onClick={this.clickSendEmail}>
                                {phase === 1 ? "전송" : "재전송" }
                            </button> : null
                        }
                        {phase === 2 ? 
                            <div className="phase2">
                                <input 
                                    type="text"
                                    id="find_certNum"
                                    className="certi_number" 
                                    placeholder="input certification number" 
                                    autoComplete="off"
                                />
                                <button className="certificate_number" onClick={this.clickCertificate}>인증</button>
                            </div> : null}
                    </div>

                    <div className="find_footer">
                        <FontAwesomeIcon
                            icon={faCircle}
                            size="xs"
                            id="close_find_btn"
                            className="md_close_btn md_find_btn"
                            onClick={this.clickClose}
                        />
                    </div>
                </div>
            </div>
        )
    }
}

export default FindModal;