import React from "react";
import "./SignupModal.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle, faSignInAlt } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
import $ from "jquery";

class SignupModal extends React.Component {
  state = {
    phase : 4
  }

  clickClose = () => {
    document.getElementById("signup_email").value = "";
    $("#signup_email").removeAttr("readOnly");
    if(this.state.phase === 3) {
      document.getElementById("signup_nickname").value = "";
      document.getElementById("signup_password").value = "";
    }
    document.getElementById("signup_modal").classList.add("md_hidden");

    $(".signup_btn").addClass("btn_hide");
    this.setState({phase : 4})
  };

  clickSignup = () => {
    const $signup = $("#signup_form");
    axios.post("/api/signup", $signup.serialize()).then((response) => {
      const {error} = response.data;
      const {email} = response.data;

      if(error) alert(error);
      if(email) alert("Sign up success!");
      this.clickClose();
    });
  };

  clickConfirmEmail = async (e) => {
    e.preventDefault();
    const email = $("#signup_email").val();
    // 중복확인 요청
    const { data } = await axios.get(`/api/signup/confirm/${email}`);
    const { isDupl } = data;
    
    if(isDupl) alert("존재하는 이메일입니다.")
    // 중복확인 성공시
    else {
      // eslint-disable-next-line no-restricted-globals
      const isUse = confirm("사용가능한 이메일입니다. 사용하시겠습니까?");
      if(isUse) {
        $("#signup_email").attr("readOnly", "ture");
        this.setState({phase : 1})
      }
    }
  }

  clickSendEmail = async (e) => {
    e.preventDefault();
    const distEmail = $("#signup_email").val();
    // eslint-disable-next-line no-restricted-globals
    const confirmEmail = confirm(`${distEmail}로 메일을 보내시겠습니까?`);
    

    if(confirmEmail) {
      const { data } = await axios.get(`/api/signup/cert/${distEmail}`);
      if(data.isSend) {
        alert("이메일을 전송하였습니다. 확인 후 인증번호를 입력해주세요.");
        this.setState({phase : 2});
      }
      else alert("다시 시도해주세요.");
    }
  }

  clickCertificate = async () => {
      const distEmail = $("#signup_email").val();
      const certNum = $("#certNum").val();
      const {data} = await axios.get(`/api/signup/cert/confirm/${distEmail}/${certNum}`);
      // 인증 성공시
      if(data.isCert) {
        this.setState({phase : 3});
        $(".signup_btn").removeClass("btn_hide");
      } else alert("다시 확인하여 주십시오.");
  }

  render() {
    const { phase } = this.state;
    return (
      <div id="signup_modal" className="signup_modal md_hidden">
        <div className="signup_wrap">
          <div className="signup_header">
            <h4><FontAwesomeIcon icon={faSignInAlt} size="lg" />&nbsp;&nbsp;Sign Up</h4>
            </div>
          <div className="signup_content">
            <form id="signup_form">
              <input
                id="signup_email"
                type="email"
                name="email"
                placeholder="email"
                autoComplete="off"
              />
              {phase === 4 ?
                <button className="confirm_email_btn" onClick={this.clickConfirmEmail}>
                  확인
                </button> : null
              }
              {phase < 3 ?
                <button className="signup_send_email" onClick={this.clickSendEmail}>
                  {phase === 1 ? "전송" : "재전송"}
                </button> : null
              }
              {phase === 3 ?
                <div>
                  <input
                    id="signup_nickname"
                    type="text"
                    name="nickname"
                    placeholder="nickname"
                    autoComplete="off"
                  />
                  <input
                    id="signup_password"
                    type="password"
                    name="password"
                    placeholder="password"
                    autoComplete="off"
                  />
                  <input
                    id="signup_confirm_password"
                    type="password"
                    name="confirmPassword"
                    placeholder="repeat password"
                    autoComplete="off"
                  />
                </div>  : null
              }
            </form>
            {phase === 2 ? 
                <div className="phase2">
                    <input 
                        type="text"
                        id="certNum"
                        className="certi_number" 
                        placeholder="input certification number" 
                        autoComplete="off"
                    />
                    <button className="certificate_number" onClick={this.clickCertificate}>인증</button>
                </div> : null
              }
          </div>
  
          <div className="signup_footer">
            <a href="/api/auth/login/naver" className="oauth_signup_naver">N</a>
            <a href="/api/auth/login/google" className="oauth_signup_google">G</a>
            <FontAwesomeIcon
              icon={faCircle}
              size="xs"
              id="close_signup_btn"
              className="md_close_btn md_signup_btn"
              onClick={this.clickClose}
            />
            <FontAwesomeIcon
              icon={faCircle}
              size="xs"
              className="signup_btn md_signup_btn btn_hide"
              onClick={this.clickSignup}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default SignupModal;
